export enum OperationStatus {
  /** File upload or processing failed */
  FAILED = 'FAILED',
  /** File is ready for consumption */
  READY = 'READY',
  /** File is waiting for processing or currently being processed */
  PENDING = 'PENDING',
}

/** @enumType */
export type OperationStatusWithLiterals =
  | OperationStatus
  | 'FAILED'
  | 'READY'
  | 'PENDING';

export enum State {
  /** File is ready for consumption */
  OK = 'OK',
  /** Deleted file */
  DELETED = 'DELETED',
}

/** @enumType */
export type StateWithLiterals = State | 'OK' | 'DELETED';

